export default function FullscreenLoader({ label = "Loading..." }) {
  return (
    <div className="min-hscreen min-h-screen flex items-center justify-center">
      <div className="flex flex-col items-center gap-3">
        <div className="h-10 w-10 rounded-full border border-white/20 border-t-white animate-spin" />
        <div className="text-sm text-white/70">{label}</div>
      </div>
    </div>
  );
}
